'use client';

import { Competency, Rating } from '@/types';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { TrustBadge } from './TrustBadge';

interface CompetencyWithTrustBadgesProps {
  competency: Competency;
  ratings: Rating[];
  average: number;
}

export function CompetencyWithTrustBadges({
  competency,
  ratings,
  average,
}: CompetencyWithTrustBadgesProps) {
  // Get unique rater types for trust badges
  const raterTypes = Array.from(new Set(ratings.map(r => r.rater_type)));

  return (
    <Card>
      <CardHeader>
        <div className="flex items-start justify-between">
          <div className="flex-1">
            <CardTitle className="text-xl">{competency.title}</CardTitle>
            {competency.description && (
              <p className="text-sm text-slate-600 mt-1">{competency.description}</p>
            )}
          </div>
          <div className="ml-4 text-right">
            <div className="text-3xl font-bold text-blue-600">{average.toFixed(1)}</div>
            <div className="text-xs text-slate-500">Average Rating</div>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {/* Trust Badges */}
          <div>
            <p className="text-sm font-medium text-slate-700 mb-2">Verified by:</p>
            <div className="flex gap-2">
              {raterTypes.map(raterType => (
                <TrustBadge key={raterType} raterType={raterType} />
              ))}
            </div>
          </div>

          {/* Rating Timeline */}
          <div>
            <p className="text-sm font-medium text-slate-700 mb-3">Rating History:</p>
            <div className="space-y-4">
              {ratings.map((rating, index) => (
                <div key={rating.id} className="relative pl-8">
                  {/* Timeline dot and line */}
                  <div className="absolute left-0 top-0 flex flex-col items-center">
                    <div className="w-3 h-3 rounded-full bg-blue-500 border-2 border-white shadow"></div>
                    {index < ratings.length - 1 && (
                      <div className="w-0.5 h-full bg-slate-200 mt-1"></div>
                    )}
                  </div>

                  {/* Rating card */}
                  <div className="bg-white rounded-lg border border-slate-200 p-4 shadow-sm">
                    {/* Header: Badge and Date */}
                    <div className="flex items-center justify-between mb-3">
                      <TrustBadge raterType={rating.rater_type} />
                      <span className="text-sm font-medium text-slate-600">
                        {rating.created_at instanceof Date
                          ? rating.created_at.toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })
                          : new Date(rating.created_at).toLocaleDateString('en-US', { month: 'short', day: 'numeric', year: 'numeric' })}
                      </span>
                    </div>

                    {/* Score */}
                    <div className="flex items-center gap-3 mb-3">
                      <span className="text-xs font-medium text-slate-500 uppercase tracking-wide">Score:</span>
                      <div className="flex items-center gap-1">
                        <span className="text-3xl font-bold text-blue-600">{rating.score}</span>
                        <span className="text-sm text-slate-400">/ 4</span>
                      </div>
                    </div>

                    {/* Comment */}
                    {rating.comment && (
                      <div className="mt-3 pt-3 border-t border-slate-100">
                        <p className="text-xs font-medium text-slate-500 uppercase tracking-wide mb-1">Comment:</p>
                        <p className="text-sm text-slate-700 leading-relaxed">{rating.comment}</p>
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
